import * as React from "react";
import CssBaseline from '@mui/material/CssBaseline';
import Avatar from "@mui/material/Avatar";
import Button from "@mui/material/Button";
import TextField from "@mui/material/TextField";
import FormControlLabel from "@mui/material/FormControlLabel";
import Checkbox from "@mui/material/Checkbox";
import Link from "@mui/material/Link";
import Grid from "@mui/material/Grid";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import Container from "@mui/material/Container";
import { createTheme, ThemeProvider } from "@mui/material/styles";
import { FilledInput, FormControl, InputLabel } from "@mui/material";
import Stack from "@mui/material/Stack";
import Snackbar from "@mui/material/Snackbar";
import MuiAlert from "@mui/material/Alert";
import { useNavigate } from "react-router-dom";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";

const theme = createTheme();
const Alert = React.forwardRef(function Alert(props, ref) {
  return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});
export default function Login() {
  const [open, setOpen] = React.useState(false);
  const [open1, setOpen1] = React.useState(false);
  const [open2, setOpen2] = React.useState(false);
  const [open3, setOpen3] = React.useState(false);
  const [status, setStatus] = React.useState(true);
  const handleClickOpen = () => {
    setOpen3(true);
  };

  const handleClose3 = () => {
    setOpen3(false);
  };

  const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }

    setOpen(false);
  };
  const handleClose1 = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }

    setOpen1(false);
  };
  const handleClose2 = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }

    setOpen2(false);
  };
  const navigation = useNavigate();
  const [name, setName] = React.useState();
  const [lastName, setLastName] = React.useState();
  const [pass, setPass] = React.useState();
  const [Email, setEmail] = React.useState();
  const [check, setCheck] = React.useState(false);
  const handleSubmit = (event) => {
    if (Email === "" || pass === "" || check === false) {
      setOpen(true);
    } else if (name !== "" || lastName !== "") {
      setOpen2(true);
    } else {
      setOpen1(true);
    }
    event.preventDefault();
  };

  React.useEffect(() => {
    if (open1 === true) {
      setInterval(() => {
        setStatus(false);
      }, 2000);
    }
  }, [open1]);

  return (
    <>
      {status ? (
        <ThemeProvider theme={theme}>
          <Container component="main" maxWidth="xs">
            <CssBaseline />
            <Box
              sx={{
                marginTop: 8,
                display: "flex",
                flexDirection: "column",
                alignItems: "center",
              }}
              style={{
                direction: "rtl",
                backgroundColor: "white",
                padding: "30px",
                borderRadius: "8px",
                marginTop: "45px",
                marginBottom: "15px",
              }}
            >
              <img
                alt="nothing to show"
                src={require("./image/firstLove.jfif")}
                style={{ width: "100px", height: "100px", borderRadius: "50%" }}
              />
              <Typography component="h1" variant="h5" sx={{ mt: 4 }}>
                  !
              </Typography>
              <Box
                component="form"
                noValidate
                onSubmit={handleSubmit}
                sx={{ mt: 4 }}
              >
                <Grid container spacing={2}>
                  <Grid item xs={12} sm={6}>
                    <FormControl variant="filled" required fullWidth autoFocus>
                      <InputLabel
                        htmlFor="outlined-adornment-password"
                        style={{
                          right: 0,
                          marginRight: "30px",
                          transformOrigin: "top right",
                          maxWidth: "80px",
                          paddingRight: "2px",
                        }}
                      >
                        
                      </InputLabel>
                      <FilledInput
                        error={name === "" && true}
                        sx={{ bgcolor: "white" }}
                        onChange={(e) => {
                          setName(e.target.value);
                        }}
                        type="text"
                        label="username"
                      />
                    </FormControl>
                  </Grid>
                  <Grid item xs={12} sm={6}>
                    <FormControl variant="filled" required fullWidth>
                      <InputLabel
                        htmlFor="outlined-adornment-password"
                        style={{
                          right: 0,
                          marginRight: "30px",
                          transformOrigin: "top right",
                          maxWidth: "80px",
                          paddingRight: "2px",
                        }}
                      >
                         
                      </InputLabel>
                      <FilledInput
                        error={lastName === "" && true}
                        sx={{ bgcolor: "white" }}
                        onChange={(e) => {
                          setLastName(e.target.value);
                        }}
                        type="text"
                        label="username"
                      />
                    </FormControl>
                  </Grid>
                  <Grid item xs={12}>
                    <FormControl variant="filled" required fullWidth>
                      <InputLabel
                        htmlFor="outlined-adornment-password"
                        style={{
                          right: 0,
                          marginRight: "30px",
                          transformOrigin: "top right",
                          maxWidth: "80px",
                          paddingRight: "2px",
                        }}
                      >
                        
                      </InputLabel>
                      <FilledInput
                        error={Email === "" && true}
                        sx={{ bgcolor: "white" }}
                        id="email"
                        onChange={(e) => {
                          setEmail(e.target.value);
                        }}
                        type="email"
                        label="username"
                      />
                    </FormControl>
                  </Grid>
                  <Grid item xs={12}>
                    <FormControl variant="filled" required fullWidth autoFocus>
                      <InputLabel
                        htmlFor="outlined-adornment-password"
                        style={{
                          right: 0,
                          marginRight: "30px",
                          transformOrigin: "top right",
                          maxWidth: "80px",
                          paddingRight: "2px",
                        }}
                      >
                         
                      </InputLabel>
                      <FilledInput
                        error={pass === "" && true}
                        sx={{ bgcolor: "white" }}
                        onChange={(e) => {
                          setPass(e.target.value);
                        }}
                        id="email"
                        type="password"
                        label="username"
                      />
                    </FormControl>
                  </Grid>
                  <Grid item xs={12}>
                    <FormControlLabel
                      control={
                        <Checkbox value="allowExtraEmails" color="primary" />
                      }
                      label="      ."
                      onChange={(e) => {
                        setCheck(e.target.checked);
                      }}
                      style={{ color: check !== false ? "black" : "red" }}
                    />
                    <div>
                      <p variant="outlined" onClick={handleClickOpen}>
                         
                      </p>
                      <Dialog
                        open={open3}
                        onClose={handleClose3}
                        aria-labelledby="alert-dialog-title"
                        aria-describedby="alert-dialog-description"
                      >
                        <DialogContent>
                          <DialogContentText
                            id="alert-dialog-description"
                            style={{ direction: "rtl" }}
                          >
                                       
                               .
                          </DialogContentText>
                        </DialogContent>
                        <DialogActions>
                          <Button onClick={handleClose3} autoFocus>
                             
                          </Button>
                        </DialogActions>
                      </Dialog>
                    </div>
                  </Grid>
                </Grid>
                <Button
                  type="submit"
                  fullWidth
                  variant="contained"
                  sx={{ mt: 3, mb: 2 }}
                  onClick={handleSubmit}
                >
                   
                </Button>
              </Box>
            </Box>
          </Container>
          <Stack spacing={2} sx={{ width: "100%" }}>
            <Snackbar open={open} autoHideDuration={6000} onClose={handleClose}>
              <Alert
                onClose={handleClose}
                severity="error"
                sx={{ width: "100%" }}
              >
                       !
              </Alert>
            </Snackbar>
            <Snackbar
              open={open2}
              autoHideDuration={6000}
              onClose={handleClose2}
            >
              <Alert
                onClose={handleClose2}
                severity="error"
                sx={{ width: "100%" }}
              >
                      !
              </Alert>
            </Snackbar>
            <Snackbar
              open={open1}
              autoHideDuration={2000}
              onClose={handleClose1}
            >
              <Alert
                onClose={handleClose1}
                severity="success"
                sx={{ width: "100%" }}
              >
                     !
              </Alert>
            </Snackbar>
          </Stack>
        </ThemeProvider>
      ) : (
        <React.Fragment>
          <CssBaseline />
          <Container maxWidth="md">
            <Box sx={{ bgcolor: "#cfe8fc", height: "100vh" }}>
              <img
                src={require("./image/will.jfif")}
                style={{ width: "100%", height: "100%" }}
              />
            </Box>
          </Container>
        </React.Fragment>
      )}
    </>
  );
}
